.nav {
  &__list {
    display: flex;
    justify-content: space-between;
    margin: 0;
    padding: 0;
  }

  &__item {
    line-height: 60px;
    position: relative;
    list-style: none;
    margin-right: 20px;
    font-weight: 500;
    font-size: 12px;
    text-transform: uppercase;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    display: flex;
    text-decoration: none;
    line-height: 60px;
    color: #{$main-accent-color};

    &:hover {
      color: #{$blue-accent};
    }
  }

  .is-active {
    color: #{$blue-accent};

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      height: 4px;
      width: 100%;
      background-color: #{$blue-accent};
      border-radius: 2px;
    }
  }

}
